:root {
    --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    --animation-duration: 250ms;
  }
  
  html {
    box-sizing: border-box;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    margin: 0;
    background-color: rgb(58, 181, 202);
    color: #212121;
  }
  input {
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 200px;
    height: 30px;
    border: 1px solid;
    border-radius: 3px;
  }
  
  .gallery {
    display: grid;
    width: 1200px;
    grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
    grid-auto-rows: 300px;
    grid-gap: 12px;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
  }
  
  .gallery__item {
    transition: transform var(--animation-duration) var(--timing-function);
    position: relative;
    background-color: rgb(52, 162, 182);
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  }
  
  .gallery__item:hover {
    transform: scale(1.05);
    background-color: rgb(63, 202, 58);
  }
  
  .gallery__image {
    height: 240px;
    width: 392px;
    object-fit: cover;
   
  }
  .stats {
    display: flex;
    justify-content: space-around;
  }